<template>
  <div class="Login-box">
    <div class="Login-img">
      <img
        src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019pILfAg7Avr1567732916.png"
      />
    </div>
    <van-form>
      <div class="lm_box">
        <div class="lm_mobile lm_input" :class="[lm_Active == 1 ? 'lm_active' : '']">
          <input
            type="text"
            v-model="mobile"
            @focus="lm_inputFcous(1)"
            @blur="lm_inputBlur()"
            placeholder="请输入手机号"
          />
          <div class="getCAPTCHA" v-show="!CAPTCHAFlag" @click="getCAPTCHA()">获取验证码</div>
          <div class="getCAPTCHAA" v-show="CAPTCHAFlag">获取验证码({{ lm_second }})</div>
          <p class="lm_toast" v-show="!lm_mobileFlag">请输入正确的手机号码</p>
        </div>
        <div class="lm_pass lm_input" :class="[lm_Active == 2 ? 'lm_active' : '']">
          <input
            type="text"
            v-model="captcha"
            @focus="lm_inputFcous(2)"
            @blur="lm_inputBlur()"
            placeholder="请输入短信验证码"
          />
          <p class="lm_toast" v-show="!lm_passFlag">请输入验证码</p>
        </div>
        <div class="lm-text">
          <span>*未注册的手机号将自动注册</span>
          <span @click="lm_toggle()">使用密码登录</span>
        </div>
        <div>
          <van-button
            round
            block
            type="info"
            native-type="submit"
            color="linear-gradient(to right, rgb(255, 146, 71),rgb(253, 88, 3))"
            @click="lm_Login()"
          >登录</van-button>
        </div>
      </div>
    </van-form>
    <div></div>
  </div>
</template>

<script>
import { Toast } from "vant"
export default {
  data() {
    return {
      mobile: "18568571773", //手机号码
      captcha: "", //验证码
      lm_Active: null,
      lm_mobileFlag: true,
      lm_passFlag: true,
      lm_flag: false,
      CAPTCHAFlag: false,
      lm_second: 60
    };
  },
  methods: {
    // /输入框获取焦点事件
    lm_inputFcous(num) {
      this.lm_Active = num;
    },
    lm_inputBlur() {
      this.lm_Active = null;
      let mobileReg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
      if (mobileReg.test(this.mobile)) {
        this.lm_mobileFlag = true;
      } else {
        this.lm_mobileFlag = false;
      }
      if (this.captcha.length == 0) {
        this.lm_passFlag = false;
      } else {
        this.lm_passFlag = true;
      }
      if (this.lm_mobileFlag && this.lm_passFlag) {
        this.lm_flag = true;
      }
    },
    lm_toggle() {
      this.$router.push("/login");
    },
    // 获取验证码
    async getCAPTCHA() {
      let mobileReg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
      if (mobileReg.test(this.mobile)) {
        this.lm_mobileFlag = true;
      } else {
        this.lm_mobileFlag = false;
        return false;
      }
      if (this.lm_mobileFlag) {
        this.CAPTCHAFlag = true;
        setInterval(() => {
          this.lm_second--;
          if (this.lm_second == 0) {
            this.lm_second = 0;
            this.CAPTCHAFlag = !this.CAPTCHAFlag;
          }
        }, 1000);
        this.lm_second = 60;
        let { data }= await this.$http.post("/api/app/smsCode", {
          mobile: this.mobile,
          sms_type: "login"
        });
        console.log(data);
        if(data.code == 201){
          Toast.fail('请勿重复获取短信验证码');
        }else if(data.code == 200){
          Toast.success('发送成功');
        }
      }

      // this.CAPTCHAFlag = !this.CAPTCHAFlag
    },
    // 登陆方法
    async lm_Login() {
      if (this.lm_flag) {
        let {data} = await this.$http.post("/api/app/login", {
          mobile: this.mobile,
          sms_code: this.captcha,
          client: 1,
          type: 2
        });
        console.log(data);
         window.sessionStorage.setItem("token",JSON.stringify(data.data.remember_token))
        if (data.data.is_new != 1) {
          this.$router.push("/mine");
        } else {
          this.$router.push("/setPass");
        }
      }
    }
    // onSubmit(values) {
    //   console.log("submit", values);
    // },
    // add() {
    //   this.$router.push({
    //     path: "/lm-sms",
    //   });
    // },
    // add1() {
    //   this.$router.push({
    //     path: "/lm-pass",
    //   });
    // },
  }
};
</script>

<style lang='scss' scoped>
.lm_toast {
  color: red;
  font-size: 0.1rem;
  position: absolute;
  left: 0;
  top: 0.4rem;
}
.getCAPTCHA {
  font-size: 0.12rem;
  color: #eb6100;
  position: absolute;
  right: 0;
  top: 0.2rem;
}
.getCAPTCHAA {
  font-size: 0.12rem;
  color: #8c8c8c;
  position: absolute;
  right: 0;
  top: 0.2rem;
}
.lm_mobile {
  position: relative;
}
.lm_box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.lm_input {
  width: 3rem;
  padding-top: 0.2rem 0;
  box-sizing: border-box;
  border-bottom: 0.01rem solid rgb(238, 238, 238);
  position: relative;
  > input {
    margin: 0.2rem 0;
    border: 0;
  }
  > input::placeholder {
    font-size: 0.16rem;
    color: #bebebe;
  }
}
.lm_active {
  border-bottom: 0.01rem solid #eb6100;
}
.Login-box {
  width: 100vw;
  height: 100vh;
  background: #ffffff;
}
.Login-img {
  width: 100%;
  height: 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Login-img img {
  width: 2.3rem;
  height: 0.6rem;
}

.lm-text {
  width: 3rem;
  font-size: 0.12rem;
  color: #999999;
  display: flex;
  justify-content: space-between;
  text-align: center;
  margin-top: 0.2rem;
}
.van-button {
  width: 3rem;
  margin-top: 0.4rem;
  // margin-left: 0.4rem;
}
.van-field {
  width: 85vw;
  text-align: center;
  margin-left: 0.3rem;
}
</style>